import React from "react";
import ReactDOM from "react-dom/client"; // Corect, folosete ReactDOM.createRoot
import { BrowserRouter } from "react-router-dom"; //  adugat
import "./index.css"; // Fiierele de stil
import "./i18n";
import App from "./App"; // Import corect componenta App
import reportWebVitals from "./reportWebVitals"; // Performana aplicaiei
import "./styles/tailwind.css"; // Stilurile Tailwind CSS
import { ThemeProvider } from "./context/themecontext";
import { AuthProvider } from "./context/authcontext";

// Iniializeaz aplicaia i o monteaz pe elementul cu id-ul "root"
const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <React.StrictMode>
    <AuthProvider>
      <ThemeProvider>
        <BrowserRouter>
          <App />
        </BrowserRouter>
      </ThemeProvider>
    </AuthProvider>
  </React.StrictMode>
);

reportWebVitals();
