// src/hooks/useStories.js
import { useState, useEffect, useCallback } from "react";
import { useTranslation } from "react-i18next";
import {
  getAllStories,
  getFeaturedStories,
  getStoryById,
  getStoriesByCategory,
  searchStories,
  getAllCategories,
  getStoryRating,
  getStoryComments,
} from "../services/storiesService";

/**
 * Hook pentru toate povetile
 */
export function useStories() {
  const { i18n } = useTranslation();
  const [stories, setStories] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const fetchStories = useCallback(async () => {
    setLoading(true);
    setError(null);

    const { data, error } = await getAllStories(i18n.language);

    if (error) {
      setError(error.message || "Failed to fetch stories");
      setStories([]);
    } else {
      setStories(data || []);
    }

    setLoading(false);
  }, [i18n.language]);

  useEffect(() => {
    fetchStories();
  }, [fetchStories]);

  return { stories, loading, error, refetch: fetchStories };
}

/**
 * Hook pentru povetile featured
 */
export function useFeaturedStories() {
  const { i18n } = useTranslation();
  const [stories, setStories] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    let mounted = true;

    async function fetch() {
      setLoading(true);
      setError(null);

      const { data, error } = await getFeaturedStories(i18n.language);

      if (!mounted) return;

      if (error) {
        setError(error.message || "Failed to fetch featured stories");
        setStories([]);
      } else {
        setStories(data || []);
      }

      setLoading(false);
    }

    fetch();
    return () => {
      mounted = false;
    };
  }, [i18n.language]);

  return { stories, loading, error };
}

/**
 * Hook pentru o singur poveste
 */
export function useStory(id) {
  const { i18n } = useTranslation();
  const [story, setStory] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    let mounted = true;

    if (!id) {
      setLoading(false);
      return;
    }

    async function fetch() {
      setLoading(true);
      setError(null);

      const { data, error } = await getStoryById(id, i18n.language);

      if (!mounted) return;

      if (error) {
        setError(error.message || "Story not found");
        setStory(null);
      } else {
        setStory(data);
      }

      setLoading(false);
    }

    fetch();

    return () => {
      mounted = false;
    };
  }, [id, i18n.language]);

  return { story, loading, error };
}

/**
 * Hook pentru poveti dup categorie
 */
export function useStoriesByCategory(categorySlug) {
  const { i18n } = useTranslation();
  const [stories, setStories] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    let mounted = true;

    async function fetch() {
      setLoading(true);
      setError(null);

      const { data, error } =
        !categorySlug || categorySlug === "all"
          ? await getAllStories(i18n.language)
          : await getStoriesByCategory(categorySlug, i18n.language);

      if (!mounted) return;

      if (error) {
        setError(error.message || "Failed to fetch stories");
        setStories([]);
      } else {
        setStories(data || []);
      }

      setLoading(false);
    }

    fetch();

    return () => {
      mounted = false;
    };
  }, [categorySlug, i18n.language]);

  return { stories, loading, error };
}

/**
 * Hook pentru cutare
 */
export function useSearchStories(query) {
  const { i18n } = useTranslation();
  const [results, setResults] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  useEffect(() => {
    let mounted = true;

    if (!query || query.length < 2) {
      setResults([]);
      setLoading(false);
      setError(null);
      return;
    }

    const timeoutId = setTimeout(async () => {
      setLoading(true);
      setError(null);

      const { data, error } = await searchStories(query, i18n.language);

      if (!mounted) return;

      if (error) {
        setError(error.message || "Search failed");
        setResults([]);
      } else {
        setResults(data || []);
      }

      setLoading(false);
    }, 300);

    return () => {
      mounted = false;
      clearTimeout(timeoutId);
    };
  }, [query, i18n.language]);

  return { results, loading, error };
}

/**
 * Hook pentru categorii
 */
export function useCategories() {
  const [categories, setCategories] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    let mounted = true;

    async function fetch() {
      setLoading(true);
      setError(null);

      const { data, error } = await getAllCategories();

      if (!mounted) return;

      if (error) {
        setError(error.message || "Failed to fetch categories");
        setCategories([]);
      } else {
        setCategories(data || []);
      }

      setLoading(false);
    }

    fetch();

    return () => {
      mounted = false;
    };
  }, []);

  return { categories, loading, error };
}

/**
 * Hook pentru rating-ul unei poveti
 */
export function useStoryRating(storyId) {
  const [rating, setRating] = useState({ average: 0, count: 0, ratings: [] });
  const [loading, setLoading] = useState(true);

  const fetchRating = useCallback(async () => {
    if (!storyId) return;

    const { data } = await getStoryRating(storyId);
    if (data) setRating(data);

    setLoading(false);
  }, [storyId]);

  useEffect(() => {
    fetchRating();
  }, [fetchRating]);

  return { rating, loading, refetch: fetchRating };
}

/**
 * Hook pentru comentariile unei poveti
 */
export function useStoryComments(storyId) {
  const [comments, setComments] = useState([]);
  const [loading, setLoading] = useState(true);

  const fetchComments = useCallback(async () => {
    if (!storyId) return;

    const { data } = await getStoryComments(storyId);
    setComments(data || []);
    setLoading(false);
  }, [storyId]);

  useEffect(() => {
    fetchComments();
  }, [fetchComments]);

  return { comments, loading, refetch: fetchComments };
}
