// src/utils/imageHelper.js

/**
 * Pentru imagini puse n CRA public folder:
 * public/images/story1.png -> "/images/story1.png"
 */

export function publicImage(path) {
  // accept "story1.png", "/images/story1.png", "images/story1.png"
  if (!path) return null;

  const p = String(path);

  if (p.startsWith("http://") || p.startsWith("https://")) return p;

  if (p.startsWith("/images/")) return p;
  if (p.startsWith("images/")) return "/" + p;

  // dac e doar numele fiierului
  if (!p.includes("/")) return `/images/${p}`;

  // fallback
  return p.startsWith("/") ? p : `/${p}`;
}

/**
 * Dac ai poveti care vin cu image_url din DB,
 * aceast funcie i d un src sigur pentru <img>.
 */
export function getStoryImageSrc(story) {
  const candidate =
    story?.image || story?.image_url || story?.imageUrl || story?.cover_url;

  return publicImage(candidate) || "/images/placeholder.png";
}
